import React from 'react';
import Title from '../components/Title';
import TextBlock from "./TextBlocks/Team.json";
import ImgText from '../components/ImgText';
const lang = window.localStorage["language"];
class BoardMembers extends React.Component {
  render() {
    return (
      <ul id="board">
      {/* {
        TextBlock.BoardMembers.map((member,index)=>{
          return (<li key={index}>
            <h3>
              {member.name}
            </h3>
            <img src={member.img} alt={member.name}/>
            {(member[lang]? member[lang] : member["en"])}
          </li>)
        })
      } */}
      {
        TextBlock.BoardMembers.map((member,index)=>{
          return (<li key={index}>
            <ImgText img={member.img}>
              <h3>
                {member.name}
              </h3>
              {(member[lang]? member[lang] : member["en"])}
            </ImgText>
          </li>)
        })
      }
</ul>
    )
  }
}

export default class Team extends React.Component {
  render () {
    return (
      <main>
        <Title background='./imgs/backdrop-about.png'>Our Team</Title>
        <BoardMembers/>
      </main>
    )
  }
}