import React from 'react';
import {Facebook, MapPin, Mail, Phone} from 'react-feather';

let socials = {
  phone: {
    link:"tel:+31612345678",
    icon: <Phone />
  },
  mail: {
    link:"mailto:contact@parentingacrossborders.org",
    icon: <Mail />
  },
  fb: {
    link:"https://www.facebook.com/Parenting-across-Borders-104148937808810",
    icon: <Facebook />
  },
  location: {
    link:"https://maps.google.com/",
    icon: <MapPin />
  }
}

let socialJsxArray = [];

for(let key in socials) {
  socialJsxArray.push(
    <a key={key} href={socials[key].link} target="_blank" rel="noreferrer">
      {socials[key].icon}
    </a>
  )
}

export default class Socials extends React.Component {
  render () {
    return (
      <div className="socials">
        {
          socialJsxArray
        }
        </div>
    )
  }
}